	module.exports = {"data":{"post":{"html":"<p>Almost exactly a year ago Christopher <a href=\"http://www.papascott.de/archives/2004/06/17/expert-opinion/\">met with the state speech expert</a> to be approved for his speech therapy kindergarten for one year. Today was the follow-up meeting to be approved for an additional six months. While we had been assured by the kindergarten that an extension would be no problem, since the extension would be mostly based on their written report, we were nonetheless nervous. </p>\n<p>We needn't have worried. While he has made excellent progress the past year, his stutter is still very pronounced, especially in stress situations. However, he's smart, outgoing and self-confident, and he usually does not become flustered by his stutter, but just keeps on talking. Self-confidence is 90% of the battle. The expert approved the extension, and was already giving us advice for school next year, recommending a regular 1st grade class (with therapy on the side) rather than a special ed group, specifically because Christopher is so sure of himself.</p>\n<p>While waiting along with us, one of the mothers told us this was just like waiting for oral exams for the <em>Abitur</em>. That didn't seem that far from the truth.</p>","fields":{"title":"Oral Exams","date":"13 Jun 2005","url":"https://papascott.de/2005-06-13-oral-exams/"}}},"pathContext":{"slug":"/2005-06-13-oral-exams/","prev":{"fields":{"title":"We're A Happy Family","date":"12 Jun 2005","slug":"/2005-06-12-were-a-happy-family/"}},"next":{"fields":{"title":"blo.gs has been acquired by yahoo!","date":"14 Jun 2005","slug":"/2005-06-14-blogs-has-been-acquired-by-yahoo/"}}}}

